import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)


import Login from '../components/login/login.vue'
import Home from '../components/home/Home.vue'
import GoodsList from '../components/goods/GoodsList.vue'
import AddGoods from '../components/goods/AddGoods.vue'
import UpGoods from '../components/goods/UpGoods.vue'
import Category from '../components/category/Category.vue'
import OrderList from '../components/order/OrderList.vue'
import OrderDeail from '../components/order/OrderDeail.vue'

export default new Router({
  routes: [
    {path: '/', component: Login},
    {
      path: '/home', 
      component: Home,
      children:[
        {path: '/goodslist', component: GoodsList},
        {path: '/addgoods', component: AddGoods},
        {path: '/upgoods', component: UpGoods},
        {path: '/category', component: Category},
        {path: '/orderlist', component: OrderList},
        {path: '/orderdeail', path: '/orderdeail/:outTradeNo/:status', component: OrderDeail},
      ]
    }
  ]
})



