// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import '../src/lib/jquery'
import '../src/lib/font-awesome-4.7.0/css/font-awesome.min.css'

 import '../src/lib/bootstrap/css/bootstrap.min.css'
 import 'bootstrap/dist/css/bootstrap.css'
//import 'bootstrap/dist/js/bootstrap.js'
 import '../src/lib/bootstrap/js/bootstrap.js'

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

import ImgInputer from 'vue-img-inputer'
import 'vue-img-inputer/dist/index.css'
Vue.component('ImgInputer', ImgInputer)

import MintUI from 'mint-ui'
Vue.use(MintUI)
import 'mint-ui/lib/style.css'

import VueResource from 'vue-resource'
Vue.use(VueResource)

// 分页
// import pageination from 'vue_pageination';
// Vue.use(pageination);

//设置请求的根路径
Vue.http.options.root = 'http://112.74.39.181:1452'
//全局设置post表单格式 application/x-www-form-urlencoded
Vue.http.options.emulateJSON = true

Vue.http.options.withCredentials = true;

Vue.http.interceptors.push(function(request, next) {//拦截器
  // 跨域携带cookie
  request.credentials = true;
  next()
})



Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  render: c => c(App),
  router
})




