<template>
    <div id="container">
        <div id="goodsList">
      <table class="table table-bordered">
        <tr>
          <th>编号</th>
          <th>类别</th>
          <th>商品图片</th>
          <th>书名</th>
          <th>作者</th>
          <th>出版社</th>
          <th>价格</th>
          <th>操作</th>
        </tr>
        <tr v-for="data in datas" :key="data.pid">
          <td>{{data.pid}}</td>
          <td>{{data.cname}}</td>
          <!-- :src=" mysrc  + `${data.headImg}`" -->
          <td>  
            <img
              width="60px"
              :src=" mysrc  + data.headImg"
            >
          </td>
          
          <td>{{data.ptitle}}</td>
          <td>{{data.pprovider}}</td>
          <td>{{data.pfirm}}</td>
          <td>{{data.pprice / 100}}</td>
          <td>
            <button class="btn btn-primary" @click="up(data.pid)">上架</button>
          </td>
        </tr>
      </table>

      <nav aria-label="Page navigation" class='nav-pagination' style="text-align:center;">
        <ul class="pagination" style="display:inline-block;">
          <li>
            <a href="#" aria-label="Previous" :class="{disable: pageNum === 1}" @click="previous">
              <span aria-hidden="true">&laquo;</span>
            </a>
          </li>
           <li v-for='page in totalPages' :key='page' style="cursor: pointer;">
            <a :class="{active2: pageNum === page }" @click="changePage(page)">{{page}}</a>
          </li>
          <li>
            <a href="#" aria-label="Next" @click="next" :class="{disable: pageNum === totalPages}">
              <span aria-hidden="true">&raquo;</span>
            </a>
          </li>
        </ul>
  </nav>
    </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui'
export default {
    data(){
      return{
        datas:[],
        pageNum: 1,
        mysrc: 'http://112.74.39.181:1453/img/',//图片路径
        totalPages: '',//页数
      }
    },
    created: function(){
      this.downGoodsList();
    },
    methods: {
      downGoodsList(){
        this.$http.post('admin/api/v1/product/getSoldOutProduct',{pageNum: this.pageNum}).then(result => {
          if(result.body.code == 'OK'){
            //console.log(result.body);
            this.datas = result.body.data.list;
            this.totalPages = result.body.data.pages;
          }else{
            Toast("加载失败！");
          }
        })
      },
      up(id){
        this.$http.post('admin/api/v1/product/up',{pid: id}).then(result => {
          if(result.body.code == 'OK'){
            Toast("该商品上架成功！");
              this.downGoodsList();
          }else{
            Toast("该商品上架失败！");
          }
        })
      },
      changePage(page){
        this.pageNum = page;
        this.downGoodsList();
      },
      previous(){
        if(this.pageNum > 1){
          this.pageNum --;
          this.downGoodsList();
        }
      },
      next(){
        if(this.pageNum < this.totalPages){
          this.pageNum ++;
          this.downGoodsList();
        }
      },
    }
}
</script>

<style scoped>
.table th,
.table td {
  vertical-align: middle;
  text-align: center;
}
.active2 {
  background-color: #337ab7 !important;
  color: #fff !important;
}
.disable {
  /* pointer-events:none; */
  cursor: not-allowed;
  opacity: 0.7;
}
</style>



