<template>
  <div>

    <div class="panel panel-primary">
      <div class="panel-heading">
        <h3 class="panel-title">添加商品分类</h3>
      </div>
      <div class="panel-body form-inline">
        <label>
          分类名称：
          <input
            type="text"
            class="form-control"
            v-model="categoryName"
          >
        </label>
        
        <input type="button" value="添加" @click="addCategory" class="btn btn-primary" style="background-color: #337ab7;color:#fff">
      </div>
    </div>

    <table class="table table-bordered table-hover">
      <thead>
        <tr class="active">
          <th>分类名称</th>
          <th>操作</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="data in datas" :key="data.cname">
          <td>{{data.cname}}</td>
          <td>
            <button class="btn btn-danger" @click="delCategory(data.cname)">删除</button>
          </td>
        </tr>
      </tbody>
    </table>

  </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
  data(){
    return {
      categoryName: '',
      datas: []
    }
  },
  created:  function () {
    this.getCategoryList();
  },
  methods: {
    getCategoryList(){
      this.$http.post('api/v1/category/getAll').then(result => {
        if(result.body.code == 'OK'){
          this.datas = result.body.data;
        }else{
          Toast("加载失败！");
        }
      })
    },
    addCategory(){
      if(this.categoryName.trim().length === 0){
        return Toast("分类名称不能为空");
      }
      this.$http.post('admin/api/v1/category/add',{cname: this.categoryName}).then(result => {
        if(result.body.code == 'OK'){
          this.getCategoryList();
          this.categoryName = '';
        }else{
          Toast("添加失败！");
        }
      })
    },
    delCategory(name){
      if(confirm("确定要删除吗？")){
        if(window.event.returnValue == true){
          this.$http.post('admin/api/v1/category/del',{cname: name}).then(result => {
            if(result.body.code == 'OK'){
              Toast("删除成功！");
              this.getCategoryList();
            }else{
              Toast("删除失败！");
            }
          })
        }
      }
    }
  }
};
</script>

<style scoped>
.table th,
.table td {
  vertical-align: middle;
}
</style>




