<template>
    <div class="panel panel-info">
      <div class="panel-heading">
        <div class="panel-title" style="color: #FF0033;">当前订单状态: 
            <span>{{ status}}</span>
            <button class="btn btn-default" style="float:right; margin-right:5px" v-show="flag" @click="deliverGoods(outTradeNo)">发货</button>
        </div>
        
      </div>

      <div class="panel-body">
          <!--收货人信息-->
				<div>
					<i class="fa fa-file" style="color: #888;"></i>&nbsp;&nbsp;
					<span>收货人信息</span>
					<table class="table table-bordered" style="margin: 10px 0 30px">
						<tr class="active">
							<th>收货人</th>
							<th>手机号码</th>
							<th>邮箱</th>
							<th>地址</th>
						</tr>
							<td>{{ address.name}}</td>
							<td>{{ address.phone}}</td>
							<td>{{ address.uemail}}</td>
							<td>{{ address.position}}</td>
					</table>
				</div>
                <!--商品信息-->
				<div>
					<i class="fa fa-file" style="color: #888;"></i>&nbsp;&nbsp;
					<span>商品信息</span>
					<table class="table table-bordered" style="margin-top: 10px;">
						<tr class="active">
							<th>商品图片</th>
							<th>商品名称</th>
							<th>价格</th>
							<th>数量</th>
							<th>小计(7.5折)</th>
						</tr>
						<tr v-for="(data,index) in relates" :key="index">
							<td>
								<img width="60px" :src=" mysrc  + data.headImg" />
							</td>
							<td>{{ data.ptitle }}</td>
							<td>{{ data.pprice / 100 }}</td>
							<td>{{ data.amount}}</td>
							<td>{{ money[index] / 100}}</td>
						</tr>
					</table>
					<div style="text-align: right; margin-right: 20px; font-size: 16px;">总计: <span style="color: #FF0033;">{{ totalFee / 100 }}</span></div>
				</div>

      </div>
 
      

    </div>
</template>

<script>
import { Toast } from 'mint-ui'
export default {
	data(){
		return {
			address: [],
			relates: [],
			mysrc: 'http://112.74.39.181:1453/img/',
			money: [],
			totalFee: '',
			status: '',
			flag: false,
			outTradeNo: ''
		}
	},
	created: function(){
		this.getDeail();
	},
	methods: {
		getDeail(){
			var outTradeNo = this.$route.params.outTradeNo;
			this.outTradeNo = outTradeNo;
			var status = this.$route.params.status;
			if(status == 0){
				this.status = '未付款'
			}
			if(status == 1){
				this.status = '等待发货';
				this.flag = true;
			}
			if(status == 2){
				this.status = '等待确认'
			}
			if(status == 3){
				this.status = '已收货'
			}
			this.$http.post('admin/api/v1/order/getOrder',{ outTradeNo: outTradeNo}).then(result => {
				console.log(result.body.data);
            	if(result.body.code == 'OK'){
					this.address = result.body.data.address;
					this.relates = result.body.data.relates;
					this.totalFee = result.body.data.totalFee * 0.75;
					for(var i = 0 ;i < result.body.data.relates.length; i++){
						this.money[i] = this.relates[i].pprice * this.relates[i].amount * 0.75;
					}
					console.log(this.money);
				}
            	
           })
		},
		//发货
        deliverGoods(outTradeNo){
          this.$http.post('admin/v1/order/delivered',{ outTradeNo: outTradeNo}).then(result => {
            if(result.body.code == 'OK'){
              Toast('发货成功！');
              this.$router.push('/orderlist');
            }
          })
        },
	}
};
</script>

<style scoped>
.table th,
.table td {
  vertical-align: middle;
}
</style>



