<template>
  <div id="container">
    <div id="searchList">
      <div class="panel panel-primary">
        <div class="panel-heading">
          <h3 class="panel-title">搜索商品</h3>
        </div>
        <div class="panel-body form-inline">
          <label>
            书名：
            <input type="text" class="form-control" v-model="keyword" @keyup="search">
          </label>
        </div>
      </div>
    </div>
    
    <div id="goodsList">
      <table class="table table-bordered">
        <tr>
          <th>编号</th>
          <th>类别</th>
          <th>商品图片</th>
          <th>书名</th>
          <th>作者</th>
          <th>出版社</th>
          <th>价格</th>
          <th>操作</th>
        </tr>
        <tr v-for="data in datas" :key="data.pid">
          <td>{{data.pid}}</td>
          <td>{{data.cname}}</td>
          <!-- :src=" mysrc  + `${data.headImg}`" -->
          <td>  
            <img
              width="60px"
              :src=" mysrc  + data.headImg"
            >
          </td>
          
          <td>{{data.ptitle}}</td>
          <td>{{data.pprovider}}</td>
          <td>{{data.pfirm}}</td>
          <td>{{data.pprice / 100}}</td>
          <td>
            <button class="btn btn-primary" @click="down(data.pid)">下架</button>
            <button class="btn btn-danger" @click="delGoodsList(data.pid)">删除</button>
          </td>
        </tr>
      </table>

      <nav aria-label="Page navigation" class='nav-pagination' style="text-align:center;">
        <ul class="pagination" style="display:inline-block;">
          <!-- 上一页 -->
          <li>
            <a href="#" aria-label="Previous" :class="{disable: pageNum === 1}" @click="previous">
              <span aria-hidden="true">&laquo;</span>
            </a>
          </li>
          <li v-for='page in totalPages' :key='page' style="cursor: pointer;">
            <a :class="{active: pageNum === page }" @click="changePage(page)">{{page}}</a>
          </li>
          <!-- 下一页 -->
          <li>
            <a href="#" aria-label="Next" @click="next" :class="{disable: pageNum === totalPages}">
              <span aria-hidden="true">&raquo;</span>
            </a>
          </li>
        </ul>
        
      </nav>
    </div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui'
export default {
    data(){
      return{
        datas:[],
        pageNum: 1,
        keyword: '',//搜索关键字
        mysrc: 'http://112.74.39.181:1453/img/',//图片路径
        flag: false,
        totalPages: '',//页数
        // value3: false
      }
    },
    created: function(){
      this.search();
    },
    methods: {
      search(){
        this.$http.post('api/v1/product/search',{ptitle:this.keyword,pageNum: this.pageNum}).then(result => {
          if(result.body.code == 'OK'){
            this.datas = result.body.data.list;
            this.totalPages = result.body.data.pages;
          }else{
            Toast("加载失败！");
          }
        })
      },
      changePage(page){
        this.pageNum = page;
        this.search();
      },
      previous(){
        if(this.pageNum > 1){
          this.pageNum --;
          this.search();
        }
      },
      next(){
        if(this.pageNum < this.totalPages){
          this.pageNum ++;
          this.search();
        }
      },
      delGoodsList(id){
        if(confirm("确定要删除吗？")){
          if(window.event.returnValue == true){
            this.$http.post('admin/api/v1/product/del',{pid: id}).then(result => {
              if(result.body.code == 'OK'){
                Toast("删除成功！");
                this.search();
                }else{
                Toast("删除失败！");
                }
              })
            }
          }
      },
      down(id){
        if(confirm("确定要下架该商品吗？")){
          if(window.event.returnValue == true){
            this.$http.post('admin/api/v1/product/down',{pid: id}).then(result => {
              if(result.body.code == 'OK'){
                Toast("该商品下架成功！");
                this.search();
                }else{
                Toast("该商品下架失败！");
                }
              })
            }
          }
      }
    }
}
</script>


<style scoped>
.table th,
.table td {
  vertical-align: middle;
  text-align: center;
}
.table th {
  color: #ccc;
}
.table td {
  color: #666;
}
.btn-default {
  background-color: #fff;
  border: 1px solid #ccc;
  color: #888;
  font-size: 12px;
}
.btn-danger {
  font-size: 12px;
}
.active {
  background-color: #337ab7 !important;
  color: #fff !important;
}
.disable {
  /* pointer-events:none; */
  cursor: not-allowed;
  opacity: 0.7;
}
</style>



