<template>
<div>
  <el-container>
    <el-aside width="200px">
      <el-menu :default-openeds="['1', '2','3']">
        <el-submenu index="1">
          <template slot="title"><i class="el-icon-goods"></i>&nbsp;&nbsp;商品</template>
          <el-menu-item index="1-1" @click="goodsList"><i class="fa fa-list-ul"></i>&nbsp;&nbsp;商品列表</el-menu-item>
          <el-menu-item index="1-2" @click="addGoods"><i class="fa fa-plus-square-o"></i>&nbsp;&nbsp;添加商品</el-menu-item>
          <el-menu-item index="1-3" @click="upGoods"><i class="fa fa-upload"></i>&nbsp;&nbsp;商品上架</el-menu-item>
          <el-menu-item index="1-4" @click="category"><i class="fa fa-qrcode"></i>&nbsp;&nbsp;商品分类</el-menu-item>
        </el-submenu>
        <el-submenu index="2">
          <template slot="title"><i class="el-icon-document"></i>&nbsp;&nbsp;订单</template>
          <el-menu-item index="2-1" @click="orderlist"><i class="fa fa-list-ul"></i>&nbsp;&nbsp;订单列表</el-menu-item>
        </el-submenu>
      </el-menu>
    </el-aside>

    <el-container style="height:626px; border:1px solid #ccc">
      <el-header style="font-size: 16px; magrin-right: 30px">
        <i class="fa fa-home"></i>
        <span>后台管理系统</span>
        <div style="font-size: 14px; magrin-right: 50px; display: inline-block; float:right">
            <span>
              <i class="fa fa-user-o" style="margin-right: 15px"></i>
              {{ username }}
            </span>&nbsp;&nbsp;&nbsp;
            <span style="cursor:pointer; color: skyblue" @click="loginout">注销</span>

        </div>
      </el-header>

      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</div>
</template>

<style>
i{
  margin-bottom: 3px;
}
.el-menu{
    background-color: #fff;
    border: 0;
}

.el-aside{
    color: #333;
}

.el-header {
  background-color: #fff;
  color: #333;
  line-height: 60px;
  border-bottom: 1px solid #ccc;
}


</style>

<script>
import { Toast } from 'mint-ui';
export default {
    data(){
      return {
        username: ''
      }
    },
    created(){
      var user = JSON.parse(localStorage.getItem('user')||'[]');
      this.username = user.mid;
      if(user.mid){
        this.goodsList();
      }else{
        this.$router.push('/');
      }
    },
    methods: {
      goodsList(){
        this.$router.push('/goodslist');
      },
      addGoods(){
        this.$router.push('/addgoods');
      },
      upGoods(){
        this.$router.push('/upgoods');
      },
      category(){
        this.$router.push('/category');
      },
      orderlist(){
        this.$router.push('/orderlist');
      },
      loginout(){
        this.$http.post('admin/api/v1/manager/logout').then(result => {
          if(result.data){
            let myUser = JSON.parse(localStorage.getItem('user') || '[]')
            //console.log(myUser);
            localStorage.clear();
            //console.log(localStorage.getItem('user'));
            this.username = '';
            Toast("退出登录");
            this.$router.push('/');
          }
        })
      },
    }
   
};
</script>

<style scoped>
.btn-default {
  background-color: #fff;
  border: 1px solid #ccc;
  color: #888;
  font-size: 12px;
}
</style>



