<template>
  <div id="container">
    <form class="form-horizontal" id="addform" ref="form" enctype="multipart/form-data">
      <div class="form-group">
        <label for="goods_classify" class="col-sm-offset-1 col-sm-2 control-label">商品分类</label>
        <div class="col-sm-6">
          <select id="goods_classify" class="form-control" v-model="cname">
            <option v-for="option in categoryList" :value="option.cname" :key="option.cname">{{option.cname}}</option>
          </select>
        </div>
      </div>
      <div class="form-group">
        <label for="goods_name" class="col-sm-offset-1 col-sm-2 control-label">书名</label>
        <div class="col-sm-6">
          <input type="text" class="form-control" id="goods_name" v-model="name">
        </div>
      </div>
      <div class="form-group">
        <label for="goods_author" class="col-sm-offset-1 col-sm-2 control-label">作者</label>
        <div class="col-sm-6">
          <input type="text" class="form-control" id="goods_author" v-model="author">
        </div>
      </div>
      <div class="form-group">
        <label for="goods_press" class="col-sm-offset-1 col-sm-2 control-label">出版社</label>
        <div class="col-sm-6">
          <input id="goods_press" type="text" class="form-control" v-model="press">
        </div>
      </div>
      <div class="form-group">
        <label for="goods_price" class="col-sm-offset-1 col-sm-2 control-label">商品售价(单位分)</label>
        <div class="col-sm-6">
          <input type="text" class="form-control" id="goods_price" v-model="price">
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-offset-1 col-sm-2 control-label">图片上传</label>


      <div id="first" style="display: inline-block;">
        <div class="fileinput-box">
  	      <i class="fa fa-photo fa-3x"></i>
  	      <input type="file" id="files1" name="files" class="fileupload" @change="changepic1(this)"/> <br />
          <img src="#" id="img1" />
        </div>
      </div>

      <div id="second" style="display: inline-block;">
        <div class="fileinput-box">
  	      <i class="fa fa-photo fa-3x"></i>
  	      <input type="file" id="files2" name="files" class="fileupload" @change="changepic2(this)"/> <br />
          <img src="#" id="img2" />
        </div>
      </div>

      <div id="three" style="display: inline-block;">
        <div class="fileinput-box">
  	      <i class="fa fa-photo fa-3x"></i>
  	      <input type="file" id="files3" name="files" class="fileupload" @change="changepic3(this)"/> <br />
          <img src="#" id="img3" />
        </div>
      </div>
        
      </div>
      <!--上传图片-->
      <div class="form-group">
        <div class="col-sm-offset-8" >
          <button type="button" class="btn btn-primary" @click="addGoods">添加</button>
          <button type="reset" class="btn btn-default">取消</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import { Toast } from 'mint-ui'
export default {
  data() {
    return {
      categoryList: [],
      name: '',
      author: '',
      press: '',
      price: '',
      file: null,
      cname: '',
    };
  },
  created(){
    this.getCategoryList()
  },
  mounted() {
  },
  methods: {
    fileChange(file, name) {
      console.log("File --> ", file);
      console.log("FileName -->", name);
    },
    onErr(err, file) {
      console.log("​onErr -> file", file);
      console.log("​onErr -> err", err);
    },
    getCategoryList(){
      this.$http.post('api/v1/category/getAll').then(result => {
        if(result.body.code == 'OK'){
          this.categoryList = result.body.data;
        }else{
          Toast("加载失败！");
        }
      })
    },
    addGoods(){
      if(this.name == '' || this.author == '' || this.press == '' || this.price == '' || this.cname == ''){
        Toast("表单中的信息不能为空！");
        return;
      }
      var _this = this;
      var formData = new FormData();
      var inputs = $("input.fileupload");
      for (var i = 0; i < inputs.length; i++) {
        var file = inputs[i];
        if (inputs[i].files[0]) {
        formData.append("files", file.files[0]);
        }
      }
      formData.append('ptitle',_this.name);
      formData.append('pprovider',_this.author);
      formData.append('pfirm',_this.press);
      formData.append('pprice',_this.price);
      formData.append('cname',_this.cname);
      
      $.ajax({
        type: "post",
        url: "http://112.74.39.181:1452/admin/api/vi/product/add",
        data: formData,
        dataType:'json',
        contentType: "multipart/form-data",
        cache: false, //上传文件不需要缓存
        processData: false, // 告诉jQuery不要去处理发送的数据
        contentType: false, // 告诉jQuery不要去设置Content-Type请求头
        success:function(result){
          console.log(result);
          if(result.code == 'OK'){
            Toast("添加成功");
            _this.name = '';
            _this.author = '';
            _this.press = '';
            _this.price = '';
            _this.cname = '';
            //document.getElementById("addform").reset();
            //Object.assign(this.$data, this.$options.data())
            window.location.reload('/addgoods');

          }else{
            alert(result.code);
            alert(result.msg);
          }
        }
      })
    },
    changepic1() {
      var reads = new FileReader();
			var f = document.getElementById('files1').files[0];
			reads.readAsDataURL(f);
			reads.onload = function(e) {
				document.getElementById('img1').src = this.result;
        $("#img1").css("opacity", 1);
        $("#first .fileinput-box").css("background-color","#fff");
        $("#first .fileinput-box i").css("opacity",0);
			};
    },
    changepic2() {
      var reads = new FileReader();
			var f = document.getElementById('files2').files[0];
			reads.readAsDataURL(f);
			reads.onload = function(e) {
				document.getElementById('img2').src = this.result;
        $("#img2").css("opacity", 1);
        $("#second .fileinput-box").css("background-color","#fff");
        $("#second .fileinput-box i").css("opacity",0);
			};
    },
    changepic3() {
      var reads = new FileReader();
			var f = document.getElementById('files3').files[0];
			reads.readAsDataURL(f);
			reads.onload = function(e) {
				document.getElementById('img3').src = this.result;
        $("#img3").css("opacity", 1);
        $("#three .fileinput-box").css("background-color","#fff");
        $("#three .fileinput-box i").css("opacity",0);
			};
    }
  }
};
</script>

<style scoped>
.btn-default {
  background-color: #fff;
  border: 1px solid #ccc;
  color: #888;
  font-size: 12px;
}
.fileinput-box {
  position: relative;
  left: 20px;
  top: 20px;
  cursor: pointer;
  margin: 0 10px 20px;
  display: inline-block;
  width: 100px;
  height: 100px;
  background-color: #ccc;
}
.fileinput-box .fa-close {
  position: absolute;
  top: 2px;
  right: 2px;
  color: #666;
}
.fileinput-box .fa-photo {
  position: absolute;
  transform: translate(-50%,-50%);
  top: 50%;
  left: 50%;
}

.fileinput-button {
  position: relative;
  display: inline-block;
  overflow: hidden;
}
.fileupload{
  position:absolute;
  left: 0px;
  top: 0px;
  opacity: 0;
  /* font-size: 200px; */
  width: 100px;
  height:100px;
  cursor: pointer;
  z-index: 2;
}

#img1,#img2,#img3 {
  width: 100px;
  position:absolute;
  top: 0;
  left: 0;
  opacity: 0;
  cursor: pointer;
}
</style>



